<template>
    <div class="base-setting">
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>各游戏封盘时间设置（以开奖时间为参照）</span>
                    <div class="operate">
                        <!-- <div style="display: inline-block;position: relative;width: 190px;height: 32px;vertical-align: middle;">
                            <i-select v-model="selectGameTimeType" @on-change="handleChangeTimeType" style="position: absolute;margin-right: 10px;width: 180px;">
                                <i-option v-for="(item, index) in gameType" :key="index" :value="item.value">{{item.title}}</i-option>
                            </i-select>
                        </div> -->
                        <div class="button" @click="handleFlushNow">立即刷新</div>
                        <span style="vertical-align: middle;" @click="handleOperate(1)">修改/保存</span>
                    </div>
                </div>
            </div>
            <div class="list">
                <div class="list-item">
                    <div>彩种</div>
                    <div>距开奖*秒</div>
                    <div style="background-color: #e4e4e4;font-size: 13px;word-break: keep-all;">当前封盘倒计时</div>
                </div>
                <template v-for="(item, index) in gameList">
                    <div v-if="item.gameType == selectGameTimeType" class="list-item" :key="index">
                        <div>{{item.name}}</div>
                        <div>
                            <InputNumber :max="120" :min="1" class="base-item-input" v-model="item.value"></InputNumber>
                        </div>
                        <div style="display: flex;align-items: center;justify-content: center;background-color: #e4e4e4;" v-text="item.countDownText"></div>
                    </div>
                </template>
            </div>
        </div>
        <div class="content">
            <div class="list-header">
                <div class="list-title">
                    <span>系统消息设置</span>
                    <div class="operate">
                        <!-- <div style="display: inline-block;position: relative;width: 190px;height: 32px;vertical-align: middle;">
                            <i-select v-model="selectGameMesType" @on-change="handleChangeMesType" style="position: absolute;margin-right: 10px;width: 180px;">
                                <i-option v-for="(item, index) in gameType" :key="index" :value="item.value">{{item.title}}</i-option>
                            </i-select>
                        </div> -->
                        <span @click="handleOperate(2)">修改/保存</span>
                    </div>
                </div>
            </div>
            <div class="list-1">
                <div class="base-list-item">
                    <div class="base-item-second">
                        <span>封盘前</span>
                        <InputNumber :max="120" :min="1" class="base-item-input" v-model="beforeSealingSecond"></InputNumber>
                        <span>秒</span>
                        <Checkbox v-model="before50.show" class="system-operate-item">
                            <span>{{before50.title}}</span>
                        </Checkbox>
                    </div>
                    <textarea v-model="beforeSealing"></textarea>
                </div>
                <div class="base-list-item">
                    <div class="base-item-second">
                        <span>封盘信息核对</span>
                        <!-- <InputNumber :max="120" :min="1" class="base-item-input" v-model="afterSealing1Second"></InputNumber>
                        <span>秒下注核对</span> -->
                    </div>
                    <textarea v-model="afterSealing1"></textarea>
                </div>
                <div class="base-list-item">
                    <div class="base-item-second">
                        <span>封盘后</span>
                        <InputNumber :max="120" :min="1" class="base-item-input" v-model="afterSealing2Second"></InputNumber>
                        <span>秒自定义</span>
                    </div>
                    <textarea v-model="afterSealing2"></textarea>
                </div>
                <div class="base-list-item">
                    <div class="base-item-second space-between">
                        <span>中奖明细</span>
                        <div class="space-right">
                            <div class="right-item" @click="editShow = true">{结算时中奖格式}</div>
                            <div class="right-item" @click="editShowNot = true">{结算时未中奖格式}</div>
                        </div>
                    </div>
                    <textarea v-model="winningDetails"></textarea>
                </div>
                <div class="base-list-item">
                    <div class="base-item-second space-between">
                        <span>会员积分</span>
                        <Checkbox v-model="showBillTable" class="system-operate-item">
                            <span>使用表单显示余额</span>
                        </Checkbox>
                    </div>
                    <textarea v-model="membershipScore"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="base-item-second">封盘提示</p>
                    <textarea v-model="sealingTips"></textarea>
                </div>
                <div class="base-list-item">
                    <p class="base-item-second">系统消息可用变量说明</p>
                    <div v-for="(item, index) in poptips" :key="index" class="poptip-item">
                        <span v-text="item.title"></span>
                        <span v-text="item.desc"></span>
                    </div>
                </div>
            </div>
        </div>
        <div v-show="editShow || editShowNot" class="article-mask">
            <div v-show="editShow || editShowNot" class="article-content">
                <div class="article-header">
                    <span>{结算} 格式内容设置</span>
                    <span class="modal-close" @click="editShow = false;editShowNot = false;">X</span>
                </div>
                <div class="article-list">
                    <i-input v-if="editShow" v-model="Settlement" type="textarea" class="column-textarea" :rows="4" :autosize="{minRows: 4,maxRows: 4}"></i-input>
                    <i-input v-if="editShowNot" v-model="NotSettlement" type="textarea" class="column-textarea" :rows="4" :autosize="{minRows: 4,maxRows: 4}"></i-input>
                    <p style="padding: 4px 0;" v-text="editShowNot ? '支持变量： {玩家}{当期盈亏}' : '支持变量： {玩家}{当期盈亏}{中奖详细}'"></p>
                    <div class="article-item" @click="handleSave(editShow ? '3' : '4')">确定</div>
                </div>
            </div>
        </div>
        <loading :loading="isLoading"></loading>
        <div style="margin: 20px 0">
            <router-view></router-view>
        </div>
    </div>
</template>

<script>
import Bus from '../../tools/eventBus/index.js';
export default {
    data() {
        return {
            poptips: [
                {
                    title: '封盘前提示可用变量',
                    desc: '{期号}{游戏}'
                },
                {
                    title: '封盘核对可用变量',
                    desc: '{期号}{游戏}{玩家数量}{玩家总分}{核对}'
                },
                {
                    title: '中奖明细可用变量',
                    desc: '{期号}{游戏}{开奖信息}{在线人数}{玩家数量}{玩家总分}{当期期号}{结算记录}{中奖记录}{中奖}{结算}'
                },
                {
                    title: '会员积分可用变量',
                    desc: '{微群名字}{在线人数}{玩家数量}{玩家总分}{账单内容}'
                },
                {
                    title: '封盘后自定义消息',
                    desc: '无可用变量'
                }
            ],
            selectGameTimeType: 'lottery',
            selectGameMesType: 'lottery',
            editShow: false,
            editShowNot: false,
            gameType: [
                {
                    value: 'lottery',
                    title: '彩票游戏设置'
                },
                {
                    value: 'video',
                    title: '视讯游戏设置'
                }
            ],
            gameList: [],
            sealingTips: '',
            winningDetails: '',
            membershipScore: '',
            beforeSealing: '',
            beforeSealingSecond: 0,
            afterSealing1: '',
            afterSealing1Second: 0,
            afterSealing2: '',
            afterSealing2Second: 0,
            Settlement: '',
            NotSettlement: '',
            isLoading: false,
            showBillTable: false,
            id: '',
            before50: {
                show: false,
                title: '禁止撤单'
            },
            interval2: null,
            startTime: 0,
            endTime: 0,
            lastTime: 0
        }
    },
    mounted() {
        // Bus.$on('BackstageGameInfo', function(data) {
        //     typeof data == 'string' && (data = JSON.parse(data));
        //     if (that.gameList.length) {
        //         let index = that.gameList.findIndex(ele => {return ele.type == data.GameType && ele.gameType == 'lottery'});
        //         let item = that.gameList[index];
        //         that.gameList.splice(index, 1, Object.assign({}, item, {countDown: data.Surplus}))
        //         console.log(that.gameList)
        //     }
        // });
        // Bus.$on('endReConnect', function() {
        //     that.lotterySignalr.changeBaseSetting();
        // })
        // Bus.$on('restart', function() {
        //     that.lotterySignalr.changeBaseSetting();
        // })
        let that = this;
        that.getGameList().then(function() {
            // that.lotterySignalr.changeBaseSetting();
            that.handleGetGameInfo();
            that.getData();
        });
        if (!that.interval2) {
            that.interval2 = setInterval(function() {
                that.countDown();
            }, 1000 - (that.endTime - that.startTime));
        }
    },
    methods: {
        handleFlushNow() {
            let that = this,
                curTime = +new Date();
            if (curTime - that.lastTime > 5000) {
                that.handleGetGameInfo().then(function() {
                    that.$Message.success('刷新成功');
                });
            } else {
                that.$Message.error('刷新间隔需要大于5秒');
            }
        },
        handleChangeTimeType(item) {
            let that = this;
            console.log(item)
        },
        countDown() {
            let that = this;
            that.startTime = +new Date();
            that.gameList.forEach(function(item) {
                if (item.countDown > 0) {
                    item.countDown--;
                    item.countDownText = ('00' + parseInt(item.countDown / 60)).slice(-2) + ':' + ('00' + (item.countDown % 60).toFixed(0)).slice(-2);
                } else {
                    item.countDownText = '0';
                }
            });
            that.endTime = +new Date();
        },
        handleChangeMesType(item) {
            let that = this;
            console.log(item, that.selectGameMesType)
            // that.selectGameMesType = item;
            that.getData();
        },
        handleGetGameInfo() {
            let that = this;
            return new Promise((resolve, reject) => {
                that.$axios({
                url: '/api/Merchant/GetGameInfos'
                }).then(function(data) {
                    let result = data.data.Data;
                    result.forEach(function(item) {
                        let ind = that.gameList.findIndex(ele => {return ele.type == item.GameType && ele.gameType == 'lottery'});
                        let el = that.gameList[ind];
                        that.gameList.splice(ind, 1, Object.assign({}, el, {countDown: item.Surplus}));
                    })
                    that.lastTime = +new Date();
                    resolve();
                })
            })
        },
        getGameList() {
            let that = this;

            return new Promise((resolve, reject) => {
                that.$axios({
                    url: '/api/Merchant/GetGameList'
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Data;

                        that.gameList = result.map(item => {
                            return {
                                name: item.GameName,
                                type: item.GameType,
                                key: item.NickName,
                                value: 0,
                                gameType: 'lottery',
                                countDown: 0,
                                countDownText: ''
                            }
                        }).sort((a, b) => a.type - b.type);
                        // that.gameList.push({
                        //     name: '百家乐',
                        //     type: '1',
                        //     key: 'bjl',
                        //     value: 0,
                        //     gameType: 'video',
                            // countDown: 0,
                            // countDownText: ''
                        // })
                        resolve();
                    }
                    reject();
                })
            })
        },
        handleOperate(type) {
            let that = this;

            switch(type) {
                case 1:
                    that.handleSave(type);
                break;
                case 2:
                    that.handleSave(type);
                break;
            }
        },
        handleSave(type) {
            let that = this,
                flag = false,
                param = {},
                href = '';

            if (!that.afterSealing2 && type === 2) {
                that.$Message.error('请填写自定义封盘后消息');
                return;
            }

            if (!that.afterSealing2Second && type === 2) {
                that.$Message.error('请填写自定义封盘后时间');
                return;
            }

            if (!that.afterSealing1 && type === 2) {
                that.$Message.error('请填写封盘后核对消息');
                return;
            }

            // if (!that.afterSealing1Second && type === 2) {
            //     that.$Message.error('请填写封盘后核对消息时间');
            //     return;
            // }

            if (!that.beforeSealing && type === 2) {
                that.$Message.error('请填写自定义封盘前消息');
                return;
            }

            if (!that.beforeSealingSecond && type === 2) {
                that.$Message.error('请填写自定义封盘前时间');
                return;
            }

            if (!that.sealingTips && type === 2) {
                that.$Message.error('请填写封盘提示');
                return;
            }

            if (!that.winningDetails && type === 2) {
                that.$Message.error('请填写中奖提示消息');
                return;
            }

            if (!that.membershipScore && type === 2) {
                that.$Message.error('请填写会员积分消息');
                return;
            }

            if (!that.Settlement) {
                that.$Message.error('请填写{中奖格式}内容');
                return;
            }

            if (!that.NotSettlement) {
                that.$Message.error('请填写{未中奖格式}内容');
                return;
            }

            that.gameList.forEach(item => {
                if (!item.value && type === 1 && !flag && item.gameType == 'lottery') {
                    flag = true;
                    that.$Message.error(`请填写${item.name}封盘时间`);
                }
            })

            if (flag) {
                return;
            }

            param['ID'] = that.id;
            (param['CustomMsg'] = that.afterSealing2); // 自定义时间
            (param['CustomTime'] = that.afterSealing2Second); // 自定义消息
            (param['EntertainedAfterMsg'] = that.afterSealing1); // 封盘后消息
            // param['EntertainedAfterTime'] = that.afterSealing1Second; // 封盘后时间
            (param['EntertainedFrontMsg'] = that.beforeSealing); // 封盘前消息
            (param['EntertainedFrontTime'] = that.beforeSealingSecond); // 封盘前时间
            (param['LotteryFrontMsg'] = that.sealingTips); // '开奖前消息
            (param['MembershipScore'] = that.membershipScore); // 会员积分
            (param['WinningDetails'] = that.winningDetails); // 中奖明细
            (param['ProhibitChe'] = that.before50.show); // 封盘前多少秒是否禁止撤单
            (param['ShowBillTable'] = that.showBillTable);
            (param['Settlement'] = that.Settlement);
            (param['NotSettlement'] = that.NotSettlement);
            param['LotteryFrontTime'] = [];
            that.gameList.forEach(item => {
                item.key != 'bjl' && param['LotteryFrontTime'].push({
                    'Type': item.type,
                    'LotteryTime': item.value
                })
            })

            if (!that.isLoading) {
                that.isLoading = true;
                href = that.selectGameMesType == 'lottery' ? '/api/Merchant/UpdateSetupInfo' : '/api/Merchant/UpdateVideoSetupInfo';
                that.$axios({
                    url: href,
                    method: 'post',
                    data: param
                }).then(function(data) {
                    that.isLoading = false;
                    that.editShow = false;
                    that.editShowNot = false;
                    if (data.data.Status === 100) {
                        that.$Message.success(data.data.Message);
                        that.getData();
                    } else {
                        that.$Message.error(data.data.Message);
                    }
                })
            }
        },
        getData() {
            let that = this,
                href = '';

            if (!that.isLoading) {
                that.isLoading = true;
                href = that.selectGameMesType == 'lottery' ? '/api/Merchant/GetSetupInfo' : '/api/Merchant/GetVideoSetupInfo';
                that.$axios({
                    url: href
                }).then(function(data) {
                    if (data.data.Status === 100) {
                        const result = data.data.Model;
                        that.afterSealing2 = result.CustomMsg;
                        that.afterSealing2Second = result.CustomTime;
                        that.Settlement = result.Settlement;
                        that.NotSettlement = result.NotSettlement;
                        that.afterSealing1 = result.EntertainedAfterMsg;
                        // that.afterSealing1Second = result.EntertainedAfterTime;
                        that.beforeSealing = result.EntertainedFrontMsg;
                        that.beforeSealingSecond = result.EntertainedFrontTime;
                        that.sealingTips = result.LotteryFrontMsg;
                        that.winningDetails = result.WinningDetails;
                        that.membershipScore = result.MembershipScore;
                        that.before50.show = result.ProhibitChe;
                        that.showBillTable = result.ShowBillTable;

                        result.LotteryFrontTime && that.gameList.forEach(item => {
                            const res = result.LotteryFrontTime.find(ele => {return ele.Type == item.type});
                            if (res) {
                                item.value = res.LotteryTime;
                            }
                        })

                        that.id = result.ID;
                        that.canEdit1 = false;
                        that.canEdit2 = false;
                    }

                    that.isLoading = false;
                })
            }
        }
    },
}
</script>

<style lang="scss" scoped>
.base-setting {
    position: relative;
    .content {
        flex-direction: column;

        .list-header {
            padding-top: 10px;

            background-color: #02b0d5;
            .list-title {
                justify-content: space-between;

                width: 100%;

                .operate {
                    .button {
                        display: inline-block;
                        margin-right: 12px;
                        width: 109px;
                        height: 28px;
                        line-height: 24px;
                        background-color: #0078ff;
                        -webkit-box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 183, 255, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e3c90;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                    }
                   & > span {
                        display: inline-block;

                        margin-right: 4px;

                        width: 116px;
                        height: 28px;
                        line-height: 28px;

                        // background-color: rgb(102, 153, 204);
                        // border-radius: 4px;
                        // border: solid 1px rgb(102, 102, 102);
                        background-color: #00e14a;
                        box-shadow: inset 0px 0px 5px 0px rgba(124, 255, 158, 0.75);
                        border-radius: 10px;
                        border: solid 1px #0e903c;
                        text-align: center;
                        cursor: pointer;
                        color: #fff;
                   }
                }
            }
        }

        .list {
            display: flex;
            align-items: center;

            margin-top: 10px;

            width: 100%;

            border: solid 1px #c9c9c9;

            .list-item {
                flex: 1 1 11%;
                max-width: 92px;

                border-right: solid 1px #c9c9c9;

                & > div {
                    height: 35px;
                    line-height: 35px;

                    background-color: #e4e4e4;
                }

                & > div:last-child {
                    padding-top: 1px;

                    line-height: unset;

                    background-color: #fff;
                }
            }

            .list-item:first-child {
                & > div:last-child {
                    line-height: 35px;
                }
            }

            .list-item:last-child {
                border-right: none;
            }
        }

        .list-1 {
            display: flex;
            align-items: center;
            flex-wrap: wrap;

            .base-list-item {
                margin-top: 10px;
                margin-right: 15px;

                width: 298px;
                height: 205px;

                border: solid 1px #d7d7d7;
                overflow: hidden;

                .base-item-second {
                    display: flex;
                    align-items: center;

                    padding-left: 5px;

                    height: 40px;
                    background-color: rgb(228, 228, 228);

                    .system-operate-item {
                        margin-left: 10px;
                    }

                    .right-item {
                        margin-right: 12px;
                        height: 21px;
                        line-height: 19px;
                        background-color: #c9c9c9;
                        border-radius: 4px;
                        border: solid 1px #999999;
                        cursor: pointer;
                        font-size: 12px;
                    }
                }

                .space-between {
                    justify-content: space-between;
                    .space-right {
                        display: flex;
                    }
                }

                & > textarea {
                    width: 100%;
                    height: 162px;

                    border: none;
                    overflow: auto;
                }
            }

            .base-list-item:nth-child(3n+3) {
                margin-right: 0;
            }

            .base-list-item:last-child {
                margin-right: 0;

                // width: 690px;
                width: 100%;
                height: 211px;

                & > p {
                    justify-content: center;
                }
                .poptip-item {
                    display: flex;

                    text-align: left;

                    & > span {
                        display: inline-flex;
                        flex-wrap: wrap;

                        padding-left: 5px;

                        // max-width: 538px;
                        line-height: 33px;

                        background-color: #f2f2f2;
                        border-right: solid 1px #d7d7d7;
                        border-bottom: solid 1px #d7d7d7;
                        box-sizing: border-box;
                    }

                    & > span:first-child {
                        width: 150px;
                    }

                    & > span:last-child {
                        flex-grow: 1;

                        border-right: 0;
                    }
                }
            }
        }
    }
    .article-mask {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;

        width: 100%;
        height: 100%;

        background-color: rgba(55,55,55,.6);

        z-index: 1000;

        .article-content {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;

            margin: auto;

            width: 280px;
            height: 200px;

            border: solid 3px #cdcdcd;
            border-radius: 4px;

            background-color: #f2f2f2;

            z-index: 1001;

            .article-header {
                display: flex;
                justify-content: space-between;

                padding: 5px;

                background-color: #ddd;

                font-weight: bold;

                .modal-close {
                    cursor: pointer;
                }
            }

            .article-list {
                margin: 10px;

                border-radius: 4px;

                .article-item {
                    background-color: #66ccff;
                    border-radius: 4px;
                    border: solid 1px #6699cc;
                    color: #fff;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>